.course-module {
    display: flex;
    min-height: 635px;
    border-bottom: 1px solid rgba(47, 47, 47, 0.15);

    @media (max-width: $screenXXL - 1) {
        min-height: 685px; }

    @media (max-width: $screenL - 1) {
        min-height: 825px; }

    @media (max-width: $screenM - 1) {
        min-height: 750px; }

    @media (max-width: $screenS - 1) {
        min-height: 700px; }

    @media (max-width: $screenXXS - 1) {
        flex-direction: column; }

    &__header {
        min-height: 210px;

        @media (max-width: $screenXXS - 1) {
            padding-right: 50px;
            min-height: 0; } }

    &__info-block {
        display: inline-block;
        vertical-align: top;
        box-sizing: border-box;
        padding-top: 65px;
        padding-bottom: 30px;
        padding-left: 8.333333%;
        padding-right: 40px;
        width: 58.368%;

        @media (max-width: $screenXXL - 1) {
            padding-right: 20px; }

        @media (max-width: $screenL - 1) {
            padding-top: 67px;
            padding-right: 0; }

        @media (max-width: $screenM - 1) {
            padding-top: 35px; }

        @media (max-width: $screenS - 1) {
            padding-left: 33px;
            padding-top: 75px; }

        @media (max-width: $screenXS - 1) {
            padding-bottom: 12px;
            width: 53.4%; }

        @media (max-width: $screenXXS - 1) {
            order: 2;
            padding-top: 0;
            padding-left: 20px;
            width: 100%; } }

    &__image-block {
        display: inline-block;
        vertical-align: top;
        width: 41.632%;
        //background-color: #c4c4c4
        overflow: hidden;

        @media (max-width: $screenL - 1) {
            padding-top: 90px; }

        @media (max-width: $screenM - 1) {
            padding-top: 0; }

        @media (max-width: $screenS - 1) {
            padding-top: 20px; }

        @media (max-width: $screenXS - 1) {
            width: 46.6%; }

        @media (max-width: $screenXXS - 1) {
            order: 1;
            padding-top: 15px;
            width: 100%;
            max-height: 299px;
            overflow: hidden; }

        @media (max-width: $screenMob - 1) {
            padding-top: 0;
            max-height: 280px; } }

    &__image {
        display: block;
        width: auto;
        max-height: 635px;

        @media (max-width: $screenXXL - 1) {
            max-height: 680px; }

        @media (max-width: $screenL - 1) {
            max-height: 530px; }

        @media (max-width: $screenM - 1) {
            max-height: 480px; }

        @media (max-width: $screenS - 1) {
            max-height: 330px; }

        @media (max-width: $screenXXS - 1) {
            max-height: 320px; } }

    &__title {
        margin: 0 0 38px 0;
        font-weight: 400;
        font-size: 49px;
        line-height: 54px;
        letter-spacing: -0.02em;

        @media (max-width: $screenXXL - 1) {
            margin-bottom: 36px; }

        @media (max-width: $screenL - 1) {
            margin-bottom: 25px;
            line-height: 51px;
            letter-spacing: -1.4px; }

        @media (max-width: $screenM - 1) {
            margin-bottom: 16px; }

        @media (max-width: $screenS - 1) {
            margin-bottom: 50px;
            font-size: 29px;
            line-height: 31px; }

        @media (max-width: $screenXS - 1) {
            margin-bottom: 19px; }

        @media (max-width: $screenXXS - 1) {
            margin-bottom: 13px;
            line-height: 32px; }

        @media (max-width: $screenMob - 1) {
            margin-bottom: 21px;
            line-height: 31px; } }

    &__label {
        position: relative;
        display: inline-block;
        vertical-align: top;
        margin: 0;
        font-style: italic;
        color: $brown;
        z-index: -1;

        &:before {
            content: "";
            position: absolute;
            top: 4px;
            left: -27px;
            width: 14px;
            height: 23px;
            background-image: url("../images/svg/flag.svg");
            opacity: 0.2;

            @media (max-width: $screenXXL - 1) {
                top: 10px; }

            @media (max-width: $screenM - 1) {
                left: -40px; }

            @media (max-width: $screenS - 1) {
                left: -4px;
                top: 2px; } }

        @media (max-width: $screenS - 1) {
            padding-left: 30px; } }

    &__body {

        @media (max-width: $screenS - 1) {
            width: 177%; }

        @media (max-width: $screenXS - 1) {
            width: 197%; }

        @media (max-width: $screenXXS - 1) {
            width: 100%; } }

    &__info {
        margin-bottom: 62px;
        display: flex;

        @media (max-width: $screenXXL - 1) {
            margin-bottom: 52px; }

        @media (max-width: $screenL - 1) {
            margin-bottom: 86px; }

        @media (max-width: $screenM - 1) {
            margin-bottom: 89px; }

        @media (max-width: $screenS - 1) {
            margin-bottom: 14px; }

        @media (max-width: $screenXXS - 1) {
            margin-bottom: 26px; }

        @media (max-width: $screenMob - 1) {
            margin-bottom: 20px; } }

    &__info-col {
        display: inline-block;
        vertical-align: top;
        box-sizing: border-box;
        padding: 13px 16px 3px 16px;
        min-width: 208px;
        font-family: $font-alt;
        border-top: 1px solid rgba(47, 47, 47, 0.15);
        border-left: 1px solid rgba(47, 47, 47, 0.15);

        @media (max-width: $screenXXL - 1) {
            padding-top: 11px;
            padding-bottom: 0; }

        @media (max-width: $screenL - 1) {
            padding-left: 13px;
            padding-right: 13px; }

        @media (max-width: $screenS - 1) {
            min-width: 177px; }

        @media (max-width: $screenXS - 1) {
            min-width: 144px; }

        @media (max-width: $screenXXS - 1) {
            padding-top: 10px;
            min-width: 53%; }

        @media (max-width: $screenMob - 1) {
            min-width: 56%; }

        &:first-child {
            padding-left: 0;
            border-left: none; } }

    &__info-col-header {
        margin: 0 0 5px 0;
        font-size: 13px;
        line-height: 17px;
        color: rgba(47, 47, 47, 0.6);

        @media (max-width: $screenXXL - 1) {
            font-size: 14px;
            line-height: 18px; }

        @media (max-width: $screenXXS - 1) {
            margin-bottom: 6; } }

    &__info-col-descr {
        margin: 0;
        font-size: 15px;
        line-height: 19px;

        @media (max-width: $screenXXL - 1) {
            font-size: 17px;
            line-height: 21px; }

        @media (max-width: $screenL - 1) {
            line-height: 19px;
            letter-spacing: -1px; }

        @media (max-width: $screenS - 1) {
            font-size: 13px;
            line-height: 16px;
            letter-spacing: 0.1px; }

        @media (max-width: $screenXXS - 1) {
            font-size: 13px;
            letter-spacing: 0.05px;
            line-height: 15px; } } }
