.page-footer {
    padding-top: 66px;

    @media (max-width: $screenM - 1) {
        padding-top: 61px; }

    &__wrapper {
        padding-left: 33px;
        padding-right: 33px;

        @media (max-width: $screenXS - 1) {
            padding-left: 0;
            padding-right: 0; } }

    &__row {
        dosplay: flex; }

    &__copyright {
        padding: 46px 54px;
        padding-bottom: 32px;
        border-top: 1px solid $lines-d;

        @media (max-width: $screenXS - 1) {
            padding: 27px 20px; }

        @media (max-width: $screenXS-s - 1) {
            padding: 11px 20px; }

        p {
            margin: 0;
            font-family: $font-alt;
            font-size: 13px;
            line-height: 17px;
            color: $textColor-l; }

        a {
            @include hover;
            color: $textColor-l; } } }

.logo-footer {
    display: block;
    padding-bottom: 16px;

    @media (max-width: $screenM - 1) {
        margin-left: 20px;
        padding-bottom: 14px; }

    svg {
        display: block;
        width: 70px;
        height: 43px;
        fill: $textColor;

        @media (max-width: $screenM - 1) {
            width: 87px;
            height: 50px; } } }


.footer-actions {
    @include list-reset;
    @include row-component;

    li {
        box-sizing: border-box;
        border: 1px solid $lines-d;
        border-left: none;
        width: 50%;

        &:nth-child(2) {
            border-right: none; } }

    a {
        @include hover;
        @include row-component;
        box-sizing: border-box;
        width: 100%;
        padding: 15px 14px;
        color: $textColor;

        @media (max-width: $screenM - 1) {
            padding-left: 9px; } }

    .icon {
        position: relative;
        margin-right: 14px;
        width: 24px;
        height: 17px; }

    svg {
        @include center;
        display: block; }

    span {
        font-family: $font-alt;
        font-size: 13px;
        line-height: 17px; } }

.subscribe-block {
    padding: 24px 20px;
    padding-bottom: 32px;

    @media (max-width: $screenM - 1) {
        padding-top: 26px;
        padding-bottom: 27px; }

    @media (max-width: $screenXS - 1) {
        padding-right: 5px; }

    &__label {
        margin: 0;
        font-family: $font-alt;
        font-weight: 700;
        font-size: 13px;
        line-height: 23px; }

    &__descr {
        margin: 0 0 12px 0;
        max-width: 260px;
        font-family: $font-alt;
        font-size: 13px;
        line-height: 17px;

        @media (max-width: $screenM - 1) {
            margin-bottom: 14px;
            line-height: 15px; } } }

.subscribe-form {

    &__field-wrapper {
        position: relative; }

    &__field {
        @include field-reset;
        box-sizing: border-box;
        display: block;
        padding: 8px 16px;
        padding-right: 40px;
        width: 100%;
        font-family: $font-alt;
        font-size: 13px;
        line-height: 18px;
        border: 1px solid $lines-d; }

    input::-webkit-input-placeholder {
      color: $textColor-l; }

    input:-moz-placeholder {
      color: $textColor-l; }

    input::-moz-placeholder {
      color: $textColor-l; }

    input:-ms-input-placeholder {
      color: $textColor-l; }

    &__submit {
        @include btn-reset;
        position: absolute;
        top: 0;
        right: 0;
        width: 40px;
        height: 34px;

        svg {
            @include center; } } }

.social-block-big {
    padding: 0 20px;
    padding-bottom: 22px;

    &__title {
        margin: 0;
        font-family: $font-alt;
        font-weight: 700;
        font-size: 13px;
        line-height: 23px;
        border-bottom: 1px solid $lines-d;

        @media (max-width: $screenM - 1) {
            padding-bottom: 5px; } }

    &__inner {
        display: flex;
        flex-wrap: wrap;
        padding-top: 18px; } }

.social-link {
    @include row-component;
    @include hover;
    margin-bottom: 13px;
    width: 50%;
    color: $textColor;

    @media (max-width: $screenM - 1) {
        margin-bottom: 15px; }

    &__icon {
        position: relative;
        width: 24px; }

    svg {
        @include centerV;
        display: block; }

    &__text {
        margin: 0;
        font-family: $font-alt;
        font-size: 13px;
        line-height: 17px; } }


