.btn {
    display: inline-block;
    vertical-align: top;
    box-sizing: border-box;
    padding: 16px 28px;
    text-align: center;
    font-family: $font;
    font-weight: 300;
    font-size: 16px;
    line-height: 25px;
    color: #fff;
    background-color: $transparent;
    border: none;
    border-radius: 0;
    outline: none;
    transition: opacity 0.4s ease;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
        opacity: 0.9; } }
